import axios from "axios";
import { useState } from "react";
import { setPosts, setUserPost, setUserPosts } from "../../Auth/reducer/AuthReducer";
import { Avatar, Button, Card, Divider, Drawer, Form, Image, Input } from "antd";
import { useDispatch } from "react-redux";
import { useTheme } from "styled-components";
import { DeleteOutlined, HeartFilled, HeartOutlined, MessageOutlined, UserOutlined } from "@ant-design/icons";

const PostCardComponent = ({ userPosts,userData,token }) => {
    const [form] = Form.useForm();
    const [open, setOpen] = useState(false);
    const BASE_URL = process.env.REACT_APP_BASE_URL;
  const dispatch = useDispatch();
  const theme = useTheme();
    const showDrawer = () => {
      // console.log(userPosts._id,'post id in card')
      setOpen(true);
    };
    const onClose = () => {
      setOpen(false);
    };
    // console.log(userPosts, "inside card");

    const fetchUserPosts = async () => {
        const response = await axios.get(
          `${BASE_URL}posts/${userData._id}/posts`,
    
          {
            headers: {
              Authorization: "Bearer " + token,
            },
            params: {
              id: userData._id,
            },
          }
        );
    
        dispatch(setUserPosts(response.data));
      };
  const likePost = async (key) => {
    console.log(userData._id);
    const response = await axios.patch(
      `${BASE_URL}posts/${key}/like`,
      { userId: userData?._id },
      {
        headers: {
          Authorization: "Bearer " + token,
        },
        params: {
          id: key,
        },
      }
    );
    // console.log(response)
    dispatch(setUserPost(response.data));
  };
  const deletePost = async (key) => {
    const response = await axios.patch(
      `${BASE_URL}posts/delete/${key}`,
      { userId: userData._id },
      {
        headers: {
          Authorization: "Bearer " + token,
        },
        params: {
          id: key,
        },
      }
    );

    dispatch(setPosts(response.data));
    await fetchUserPosts();
  };
    const handleDelete = async (key) => {
      // console.log(key);
      const response = await axios.patch(
        `${BASE_URL}posts/${userPosts._id}/comment`,
        { comment: key.comment, userId: userData._id },

        {
          headers: {
            Authorization: "Bearer " + token,
          },
          params: {
            id: userPosts._id,
          },
        }
      );

      dispatch(setUserPost(response.data));
    };
    const onFinish = async (values) => {
      // console.log(values);
      const response = await axios.post(
        `${BASE_URL}posts/${userPosts._id}/comment`,
        { comment: values.comment, userId: userData._id },

        {
          headers: {
            Authorization: "Bearer " + token,
          },
          params: {
            id: userPosts._id,
          },
        }
      );
      // console.log(response.data);
      dispatch(setUserPost(response.data));
    //   fetchUserPosts();
      form.resetFields();
    };
    return (
      <Card
        style={{ background: theme.light, color: theme.dark, margin: "10px 0" }}
      >
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",

            alignItems: "center",
          }}
        >
          <div
            style={{
              display: "flex",
              alignItems: "center",
              gap: "20px",
            }}
          >
            <Avatar
              size={32}
              style={{ background: theme.dark }}
              icon={
                userPosts?.userPicturePath === "" ? (
                  <UserOutlined />
                ) : (
                  <Image preview={false} src={userPosts?.userPicturePath} />
                )
              }
            />
            <div>
              <h5>{userPosts?.firstName + " " + userPosts?.lastName}</h5>
              <p style={{ fontSize: "0.50rem" }}>
                {userPosts.location ? userPosts.location : "location"}
              </p>
            </div>
          </div>

          <Avatar
            size={32}
            style={{ background: theme.danger }}
            icon={<DeleteOutlined />}
            onClick={() => deletePost(userPosts._id)}
          />
        </div>
        <div style={{ padding: "10px", fontSize: "0.75rem" }}>
          {userPosts?.description}
        </div>
        <div>
          <Image
            style={{ borderRadius: "10px" }}
            src={userPosts?.picturePath}
            preview={false}
          />
        </div>
        <Divider
          style={{ borderColor: theme.darkSecondary }}
          type="horizontal"
        />
        <div
          style={{
            display: "flex",
            alignItems: "flex-start",
            textAlign: "center",
            gap: "20px",
          }}
        >
          <div onClick={() => likePost(userPosts._id)}>
            {userData._id in userPosts.likes ? (
              <HeartFilled style={{ color: "#FF5D5D" }} />
            ) : (
              <HeartOutlined />
            )}
            <div>
              {" "}
              {Object.keys(userPosts.likes)?.length || 0}
              &nbsp;likes
            </div>
          </div>
          <div>
            <MessageOutlined onClick={showDrawer} />

            <Drawer
              title="All comments"
              placement="right"
              onClose={onClose}
              open={open}
              style={{
                background: theme.light,
                color: theme.dark,
              }}
            >
              <div
                style={{
                  height: "80vh",
                  display: "flex",
                  alignItems: "center",
                  position: "relative",
                  overflow: "hidden",
                }}
              >
                <div
                  style={{
                    position: "absolute",
                    top: 0,
                    left: 0,
                    right: 0,
                    textAlign: "center",
                  }}
                >
                  All Comments
                  <Divider
                    style={{ borderColor: theme.darkSecondary }}
                    type="horizontal"
                  />
                </div>
                <div
                  style={{
                    height: "80%",
                    width: "100%",
                    margin: "10px",
                    overflow: "auto",
                  }}
                >
                  {userPosts.comments?.map((props, i) => {
                    // console.log(props, "props");
                    return (
                      <div
                        key={i}
                        style={{
                          display: "flex",
                          alignItems: "center",
                          justifyContent: "space-between",
                          padding: "10px",
                        }}
                      >
                        <div
                          style={{
                            display: "flex",
                            alignItems: "center",
                            gap: "20px",
                          }}
                        >
                          <Avatar
                            size={48}
                            style={{ background: theme.dark }}
                            icon={
                              props?.picturePath === "" ? (
                                <UserOutlined />
                              ) : (
                                <Image
                                  preview={false}
                                  src={props?.picturePath}
                                />
                              )
                            }
                          />
                          <div>
                            <h4>{props?.firstName + " " + props?.lastName}</h4>
                            <p>{props?.comment}</p>
                          </div>
                        </div>
                        <div>
                          {props?.userId === userData._id ? (
                            <DeleteOutlined
                              onClick={() => handleDelete(props)}
                            />
                          ) : (
                            ""
                          )}
                        </div>
                      </div>
                    );
                  })}
                </div>
                <div
                  style={{
                    position: "absolute",
                    bottom: 0,
                    left: 0,
                    right: 0,
                    textAlign: "center",
                  }}
                >
                  <Divider
                    style={{ borderColor: theme.darkSecondary }}
                    type="horizontal"
                  />
                  <Form
                    form={form}
                    name="horizontal_login"
                    layout="inline"
                    onFinish={onFinish}
                  >
                    <Form.Item
                      name="comment"
                      rules={[
                        {
                          required: true,
                          message: "Please Enter something to comment",
                        },
                      ]}
                    >
                      <Input
                        // onChange={(e) => setComments(e.target.value)}
                        style={{ borderRadius: "50px" }}
                        placeholder="write comments..."
                      />
                    </Form.Item>
                    <Form.Item>
                      <Button
                        type="primary"
                        // disabled={comments === "" ? true : false}
                        shape="round"
                        htmlType="submit"
                      >
                        comment
                      </Button>
                    </Form.Item>
                  </Form>
                </div>
              </div>
            </Drawer>

            <div>
              {(userPosts && userPosts?.comments?.length) || 0}&nbsp;comments
            </div>
          </div>
        </div>
      </Card>
    );
  };

  export default PostCardComponent