import {
  Avatar,
  Button,
  Card,
  Col,
  Divider,
  Drawer,
  Form,
  Image,
  Input,
  Row,
  Tabs,
  Tooltip,
} from "antd";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import {
  setAllUsers,
  setFollowers,
  setFollowing,
  setPost,
  setPosts,
  setUser,
  setUserPost,
  setUserPosts,
} from "../Auth/reducer/AuthReducer";
import { useTheme } from "styled-components";
import {
  DeleteOutlined,
  EditOutlined,
  EyeOutlined,
  HeartFilled,
  HeartOutlined,
  LinkedinFilled,
  MessageOutlined,
  TwitterOutlined,
  UserAddOutlined,
  UserDeleteOutlined,
  UserOutlined,
} from "@ant-design/icons";
import { GoLocation } from "react-icons/go";
import { BsBriefcase } from "react-icons/bs";
import { Container } from "./style";
import NavBarComponent from "../NavBar/NavBar";
import PostCardComponent from "./Partials/PostCard";

const ProfilePage = () => {
  const navigate = useNavigate();

  const [form] = Form.useForm();
  const [AllUserPosts, setAllUserPosts] = useState([]);
  const userData = useSelector((state) => state.Auth.user);
  const token = useSelector((state) => state.Auth.token);
  const AllUsers = useSelector((state) => state.Auth.AllUsers);
  const followers = useSelector((state) => state.Auth.followers);
  const following = useSelector((state) => state.Auth.following);
  const UserPosts = useSelector((state) => state.Auth.userPosts);
  const BASE_URL = process.env.REACT_APP_BASE_URL;
  const dispatch = useDispatch();
  const theme = useTheme();

  // const userData = useSelector(state=>state.Auth.user)
  // console.log(AllUserPosts, "All userr posts");
  // console.log(followers);
  // console.log(following);
  // console.log(AllUsers);
  useEffect(() => {
    fetchUserPosts();
    fetchfollowers();
    fetchfollowing();
    fetchAllUsers();
    // setAllUserPosts(UserPosts);
  }, []);

  useEffect(() => {
    setAllUserPosts(UserPosts);
  }, [UserPosts]);

  const addFriend = async (key) => {
    const response = await axios.patch(
      `${BASE_URL}user/${userData?._id}/${key?.id}`,
      {},

      {
        headers: {
          Authorization: "Bearer " + token,
        },
        params: {
          id: userData._id,
          friendId: key?.id,
        },
      }
    );
    console.log(response);
    dispatch(setUser(response.data));
    fetchfollowers();
    fetchfollowing();
    // dispatch(setFollowing(response.data.formattedfollowing));
    // dispatch(setFollowers(response.data.followers));
  };
  const fetchfollowers = async () => {
    const response = await axios.get(
      `${BASE_URL}user/${userData._id}/Followers`,

      {
        headers: {
          Authorization: "Bearer " + token,
        },
        params: {
          id: userData._id,
        },
      }
    );

    dispatch(setFollowers(response.data));
  };
  const fetchfollowing = async () => {
    const response = await axios.get(
      `${BASE_URL}user/${userData._id}/following`,

      {
        headers: {
          Authorization: "Bearer " + token,
        },
        params: {
          id: userData._id,
        },
      }
    );

    dispatch(setFollowing(response.data));
  };
  const fetchUserPosts = async () => {
    const response = await axios.get(
      `${BASE_URL}posts/${userData._id}/posts`,

      {
        headers: {
          Authorization: "Bearer " + token,
        },
        params: {
          id: userData._id,
        },
      }
    );

    dispatch(setUserPosts(response.data));
  };
  const fetchAllUsers = async () => {
    const response = await axios.get(
      `${BASE_URL}user/all/${userData._id}`,

      {
        headers: {
          Authorization: "Bearer " + token,
        },
        params: {
          id: userData._id,
        },
      }
    );
    // console.log(response);
    dispatch(setAllUsers(response.data));
  };

  const FollowingComponent = () => {
    console.log(following)
    return (
      <div
        style={{
          background: theme.light,
          color: theme.darkSecondary,
        }}
      >
        {following?.length > 0 ? (
          
          following?.map((following, i) => {
            return (
              <div
                key={i}
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "space-between",
                  margin: "10px",
                }}
              >
                <div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    gap: "20px",
                  }}
                >
                  <div>
                    <Avatar
                      style={{ background: theme.dark }}
                      size={48}
                      icon={
                        following.picturePath === "" ? (
                          <UserOutlined />
                        ) : (
                          <Image preview={false} src={following.picturePath} />
                        )
                      }
                    />
                  </div>
                  <div>
                    <h4>{following?.firstName + " " + following?.lastName}</h4>
                    <p style={{ fontSize: "0.50rem" }}>
                      {following?.occupation}
                    </p>
                  </div>
                </div>
                <div>
                  <Button
                    // type="default"
                    danger
                    shape="circle"
                    onClick={() => addFriend({ id: following._id })}
                    // style={{ background: theme.danger }}
                    icon={<DeleteOutlined />}
                  />
                </div>
              </div>
            );
          })
        ) : (
          <p>No Following</p>
        )}
      </div>
    );
  };
  const FollowersComponent = () => {
    return (
      <div
        style={{
          background: theme.light,
          color: theme.darkSecondary,
        }}
      >
        {/* followers */}

        {followers?.length > 0 ? (
         
          followers?.map((followers, i) => {
            return (
              <div
                key={i}
                style={{
                  display: "flex",
                  alignItems: "center",
                  gap: "20px",
                  margin: "10px",
                }}
              >
                <div>
                  <Avatar
                    style={{ background: theme.dark }}
                    size={48}
                    icon={
                      followers?.picturePath === "" ? (
                        <UserOutlined />
                      ) : (
                        <Image preview={false} src={followers.picturePath} />
                      )
                    }
                  />
                </div>
                <div>
                  <h4>{followers?.firstName + " " + followers?.lastName}</h4>
                  <p style={{ fontSize: "0.50rem" }}>{followers?.occupation}</p>
                </div>
              </div>
            );
          })
        ) : (
          <p>No Followers</p>
        )}
      </div>
    );
  };
  const AllUsersComponent = () => {
    return (
      <>
        <div
          style={{
            background: theme.light,
            color: theme.darkSecondary,
          }}
        >
          {/* All users */}

          {AllUsers?.length > 0 ? (
            AllUsers &&
            AllUsers?.map((AllUsers, i) => {
              return (
                <div
                  key={i}
                  style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "space-between",
                    margin: "10px",
                  }}
                >
                  <div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      gap: "20px",
                    }}
                  >
                    <div>
                      <Avatar
                        style={{ background: theme.dark }}
                        size={48}
                        icon={
                          AllUsers?.picturePath === "" ? (
                            <UserOutlined />
                          ) : (
                            <Image preview={false} src={AllUsers.picturePath} />
                          )
                        }
                      />
                    </div>
                    <div>
                      <h4>{AllUsers?.firstName + " " + AllUsers?.lastName}</h4>
                      <p style={{ fontSize: "0.50rem" }}>
                        {AllUsers?.occupation}
                      </p>
                    </div>
                  </div>
                  <div>
                    <Avatar
                      onClick={() => addFriend({ id: AllUsers._id })}
                      style={{ background: theme.primary }}
                      icon={
                        userData?.following?.includes(AllUsers._id) ? (
                          <UserDeleteOutlined />
                        ) : (
                          <UserAddOutlined />
                        )
                      }
                    />
                  </div>
                </div>
              );
            })
          ) : (
            <p>No Users</p>
          )}
        </div>
      </>
    );
  };

  return (
    <Container style={{ padding: "10px" }}>
      <NavBarComponent data={userData} />
      <div
        style={{
          marginTop: "60px",
          height: "calc(100vh - 80px)",
          // overflow: "hidden",
        }}
      >
        <Row gutter={[24, 12]}>
          <Col
            md={12}
            span={24}
            style={{ height: "calc(100vh - 80px)", overflow: "auto" }}
          >
            <Row gutter={[24, 24]}>
              <Col span={24}>
                <Card
                  style={{
                    background: theme.light,
                    color: theme.darkSecondary,
                  }}
                >
                  <div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "space-between",
                    }}
                  >
                
                      <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          flexDirection: "column",
                        }}
                      >
                        <div>
                          <Avatar
                            style={{ background: theme.dark }}
                            size={48}
                            icon={
                              userData.picturePath === "" ? (
                                <UserOutlined />
                              ) : (
                                <Image
                                  preview={false}
                                  src={userData.picturePath}
                                />
                              )
                            }
                          />
                        </div>
                        <div>
                          <h4 style={{ color: theme.primary }}>
                            {userData?.firstName}&nbsp;{userData?.lastName}
                          </h4>
                        </div>
                      </div>

                      <div style={{textAlign:'center'}}><p>{UserPosts?.length || 0}</p> <p>posts</p></div>
                      <div style={{textAlign:'center'}}><p>{userData?.followers?.length || 0}</p> <p>followers</p></div>
                      <div style={{textAlign:'center'}}><p>{userData?.following?.length || 0}</p> <p>following</p></div>
                      
                    </div>
                  
                  <Divider
                    style={{ borderColor: theme.darkSecondary }}
                    type="horizontal"
                  />
                  <div style={{ fontSize: "0.75rem" }}>
                    <div
                      style={{
                        display: "flex",
                        alignItems: "center",
                        gap: "20px",
                      }}
                    >
                      <GoLocation />
                      {userData.location
                        ? userData.location
                        : "Location Not Added"}
                    </div>
                    <div
                      style={{
                        display: "flex",
                        alignItems: "center",
                        gap: "20px",
                      }}
                    >
                      <BsBriefcase />
                      {userData.occupation
                        ? userData.occupation
                        : "occupation Not Added"}
                    </div>
                  </div>
                  {/* <Divider style={{borderColor:theme.darkSecondary}} type="horizontal" />
<div style={{fontSize:'0.75rem' }}>
  <div>Profile views : {data?.viewedProfile}</div>
  <div>Impressions : {data?.impressions}</div>
</div> */}
                  <Divider
                    style={{ borderColor: theme.darkSecondary }}
                    type="horizontal"
                  />
                  <div>
                    <h5>Social Profiles</h5>
                    <div
                      style={{
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "space-between",
                      }}
                    >
                      <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          gap: "20px",
                        }}
                      >
                        <TwitterOutlined />
                        <div>
                          <h6>Twitter</h6>
                          <p style={{ fontSize: "0.75rem" }}>Social Network</p>
                        </div>
                      </div>
                      <EditOutlined />
                    </div>
                    <div
                      style={{
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "space-between",
                      }}
                    >
                      <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          gap: "20px",
                        }}
                      >
                        <LinkedinFilled />
                        <div>
                          <h6>Linkedin</h6>
                          <p style={{ fontSize: "0.75rem" }}>Network</p>
                        </div>
                      </div>
                      <EditOutlined />
                    </div>
                  </div>
                </Card>
              </Col>
              <Col span={24}>
                <Card
                  style={{
                    background: theme.light,
                    color: theme.darkSecondary,
                  }}
                >
                  <Tabs
                    defaultActiveKey="1"
                    centered
                    style={{ color: theme.dark }}
                    items={[
                      {
                        label: `Following`,
                        key: 1,
                        children: <FollowingComponent />,
                      },
                      {
                        label: `Followers`,
                        key: 2,
                        children: <FollowersComponent />,
                      },
                      {
                        label: `All Users`,
                        key: 3,
                        children: <AllUsersComponent />,
                      },
                    ]}
                  />
                </Card>
              </Col>
            </Row>
          </Col>
          <Col md={12} span={24}>
            <Card
              style={{
                background: theme.light,
                color: theme.darkSecondary,
              }}
            >
              {AllUserPosts?.length > 0 ? (
                <p style={{ textAlign: "center" }}>All Posts</p>
              ) : (
                <p style={{ textAlign: "center" }}>No posts</p>
              )}
            </Card>

            <div
              style={{ height: "500px", overflow: "auto", marginTop: "10px" }}
            >
              {AllUserPosts?.length > 0
                ? AllUserPosts?.map((userPosts, i) => {
                    return (
                      <PostCardComponent
                        key={i}
                        userData={userData}
                        token={token}
                        userPosts={userPosts}
                      />
                    );
                  })
                : ""}
            </div>
          </Col>
        </Row>
      </div>
    </Container>
  );
};

export default ProfilePage;
